<template>
    <div>
        <el-row class="title">
            <el-col :span="10">
                <b>{{options.title}}：</b>
            </el-col>
            <el-col :span="10">
                <el-button :plain="true" type="info" size="mini" @click="click_button">{{options.button}}</el-button>
            </el-col>
        </el-row>
        <el-row class="description">
            <div>
                <div v-if="options.description.extra && options.description.extra.length>0">

                    <Groups v-for="item in options.description.extra" :option="item"/>
                </div>

                <p>{{options.description.content}}</p>
            </div>
        </el-row>
    </div>
</template>

<script>
    import Groups from './Groups.vue'

    export default {
        name: 'post_item',
        components: {Groups},
        props: ['options'],
        data() {
            return {}
        },
        methods: {
            click_button() {
                this.$message({
                    message: '恭喜你，这是一条成功消息',
                    type: 'success'
                });
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1, h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    .el-button--info.is-plain {
        background: #fff;
        border-color: #50bfff;
        color: #50bfff;
    }

    .el-button--info.is-plain:hover {
        background: #fff;
        border-color: #47a6df;
        color: #47a6df;
    }

    .title {
        text-align: left;
    }

    .description {
        text-align: left;
        margin-top: 5px;
        margin-bottom: 10px;
        padding-left: 15px;
        padding-bottom: 5px;
        border-bottom: 1px dotted lightgrey;
    }
</style>



